.loadingContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  margin-top: -2vw; }

.button {
  font-weight: bold;
  padding: .5vw 1vw;
  border-radius: 20px;
  font-size: .8vw;
  border: none;
  background: white;
  text-decoration: none;
  color: black;
  transition: all .2s ease-in-out; }
  @media (max-width: 599px) {
    .button {
      font-size: .8vh;
      padding: .5vh 1vh; } }
  .button:hover {
    opacity: .8;
    cursor: pointer; }
  .button:visited {
    color: black; }

a {
  text-decoration: none; }

@keyframes backgroundBooklickColors {
  0% {
    background-color: #F6416C; }
  33% {
    background-color: #2ABDEF; }
  66% {
    background-color: #1FCFCB; }
  100% {
    background-color: #F6416C; } }

.card {
  width: 20vw;
  height: 25vw;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  margin: 1vw;
  color: black;
  box-shadow: 0 0 1vw -.5vw black;
  transition: all .2s ease-in-out; }
  @media (max-width: 599px) {
    .card {
      width: 80vw;
      height: 50vh; } }
  .card:hover {
    box-shadow: 0 0 1vw -.2vw black;
    cursor: pointer; }
  .card .imgContainer {
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vw;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 10%; }
    @media (max-width: 599px) {
      .card .imgContainer {
        height: 15vh;
        width: 15vh; } }
    .card .imgContainer img {
      width: 150%; }
